@use 'variables' as *;

.modal-tracker-card {
   display: none;  // схована за замовчуванням
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   background: rgba(0, 0, 0, 0.5);
   justify-content: center;
   align-items: center;
   z-index: 2;

   .modal-content {
      position: relative;
      max-width: 783px;
      width: 80%;
      background: $grey-blue20;
      border-radius: 40px;
      padding: 24px;

      .modal-body {
         height: auto;
         background-color: $grey-blue20;
         border-radius: 20px;
         margin-bottom: 20px;

         .modal-card-content {
            margin-bottom: 32px;
         }

         .job-title {
            background-color: #fff;
            border-radius: 32px;
            display: flex;
            flex-direction: column;
            padding: 24px;
            margin-bottom: 24px;

            .position-and-control {
               margin-bottom: 27px;
   
               .position {
                  font-size: 30px;
                  font-weight: 700;
                  line-height: 1.27;
               }
   
               .control-buttons {
                  display: none;
               }
            }

            .job-details {
               display: flex;
               gap: 26px;
               margin-bottom: 16px;
   
               .items {
                  display: flex;
                  align-items: center;
   
                  img {
                     width: 24px;
                     margin-right: 4px;
                  }
   
                  .item {
                     font-size: 18px;
                     line-height: 1.56;
                     color: $secondary-color;
                  }
               }
            }
   
            .match {
               width: 135px;
               height: 36px;
               font-size: 18px;
               line-height: 1.56;
               color: $secondary-color;
               text-align: center;
               border: 0.5px solid $blue70;
               border-radius: 18px;
               padding: 4px 12px;
            }
         }

         .characteristic-name {
            padding: 0 24px;
            margin-bottom: 24px;

            .title {
               font-size: 18px;
               font-weight: 600;
               line-height: 1.56;
               color: $grey-blue80;
               margin-bottom: 8px;
            }
         }

         .format,
         .required-skills-item,
         .descripion {
            font-size: 18px;
            line-height: 1.56;
            color: $secondary-color;
         }
         
         .required-skills {
            .required-skills-list {
               .required-skills-item {
                  display: flex;
                  margin-bottom: 8px;
                  
                  img {
                     width: 4px;
                     margin-right: 8px;
                  }
               }
            }
         }

         .job-description {
            .descripion {
               word-break: break-word; /* переносить довгі слова */
               overflow-wrap: break-word; /* сучасний аналог для довгих слів */
            }
         }
      }

      .btn-primary {
         display: block;
         padding: 13px 61px;
         margin: 0 auto;
      }
   }
}